<template>
        <div class="cart-content">
             <top-bar topTitle=""></top-bar>
             <div ref='noProduct' :class="payment.isEmpty?'W_car_box show':'W_car_box'">
							<div class="W_car">
								<img src="../../assets/images/W_car.png"/>
							</div>
							<p></p>
							<a href="javascript:;" class="goshopping"> >></a>
			 </div>
             <div class="pay-detail">
             	<ul class="mui-table-view">
					<li class="mui-table-view-cell cart-item" v-for="(goods,index) in goodsList" :key="goods.id">
						<div class="mui-slider-right mui-disabled">
							<a href="javascript:;" class="mui-btn mui-btn-red" :data-index="index"></a>
						</div>
						<div class="mui-slider-handle">
							<div class="cart-product-content">
										<div class="product-selected">
  											<input type="checkbox" :value="goods.id" :checked="goods.isPicked"/>
  											<label :data-index="index" for="checkbox" class="picked"></label>
										</div>
										<div class="product-pic">
                                            <a href="javascript:;" class="pic" :data-id="goods.id">
											<img v-lazy="domain+goods.img_url" :key="goods.img_url"/>
                                            </a>
										</div>
										<div class="product-detail">
											<p class="product-title">{{goods.title}}</p>
											<div class="product-nums">
												<p class="product-count"><span>{{goods.num}}</span></p>
												<span class="product-money"><span>{{goods.sell_price*goods.num}}</span></span>
											</div>
											<div class="product-change-count">
												<span data-sub="true" :data-index="index">-</span>
												<span>{{goods.num}}</span>
												<span data-add="true" :data-index="index">+</span>
											</div>
										</div>
									</div>
						</div>
					</li>
				</ul>
             </div>
             <div class="pay-content">
		        	<div class="all">
  						<input type="checkbox" :checked="isAllSelected">
  						<label for="selected-all" class="chekbox-all"></label>
					</div>
		        	<span class="selected-all"></span>
		        	<a href="javascript:;" class="go-pay">({{payment.num}})</a>
		        	<span class="total-money"><span>&nbsp:&nbsp</span><i>{{payment.sum}}</i></span>
	        </div>
        </div>
</template>
<script>
	//
	import localStorageTool from '../common/localstorageTool.js';
	//
	import connect from '../common/connect.js';
    export default {
     	data(){
     		return {
     			goodsList:[],//
     			domain:'',
     		}
     	},
     	created(){
     		if(process.env.NODE_ENV == 'production'){
                this.domain = '/view.jw.design.io';
            }
			//localStorageTool.saveProds({"57":2,"58":1});///**/
     		//local
        	let prods = localStorageTool.getProds(); 
        	//return

        	if(Object.keys(prods).length === 0)return;
        	//prods{"87":4,"89":10,"93":11,"95":16} id:count
        	let ids = Object.keys(prods);
        	//
        	this.$ajax('cart-products-list.json')
        		.then(res=>{
            		let allGoods = res.data;
            		ids.forEach((ele,index)=>{
            	 		this.goodsList.push(allGoods.find(function(item){
            				return item.id === parseInt(ele);
            			}));
            		});

             		//prods{"87":4,"89":10,"93":11,"95":16}   ele.id-> 87
             		//num
            		this.goodsList.forEach((ele,index)=>{
                 		if(prods[ele.id]){ //
                    		//vue
							//this.$set
                    		this.$set(ele,'num',prods[ele.id]);
                    		this.$set(ele,'isPicked',true);
                		}
            		});
        		})
        		.catch(err=>{
            		console.log(err);
        		});
     	},
     	mounted(){
     		var that = this;
     		let prods = localStorageTool.getProds(); 

            mui('.W_car_box').on('tap','.goshopping', function(e){
                that.$router.push({name:'category_grides',query:{dataId:16,from:'cart'}});
            });

        	//return
        	if(Object.keys(prods).length === 0){
        		this.$refs.noProduct.classList.add('show');
        		return;
        	} else {
        		this.$refs.noProduct.classList.remove('show');
        	}


     		mui.init();
     		mui('.pay-detail').on('tap', '.mui-btn', function (e) {
        		var li = this.parentNode.parentNode;
        		var index = parseInt(this.getAttribute('data-index'));
        		mui.confirm('', '', ['', ''], function (e) {
            		if (e.index == 0) {
                		that.del(index, function (data) {
                    		if (data == true) {
                        		mui.toast('');
                    		} else if (data.error) {
                        		mui.toast(data.message);
                    		}
                		});
            		} else {
                		mui.swipeoutClose(li);
            		}
        		});
    		});
    		mui('.pay-detail').on('tap','.product-change-count span', function(e){
    			let index = this.getAttribute('data-index');
    			if (this.getAttribute('data-sub')){
    				that.sub(index);
    			}
    			if (this.getAttribute('data-add')){
    				that.add(index);
    			}
    		});
    		mui('.pay-content').on('tap','.chekbox-all', function(e){
    			 that.selectedAll();
    		});
    		mui('.pay-detail').on('tap','.picked', function(e){
    			  let index = this.getAttribute('data-index');
    			  that.goodsList[index].isPicked = !that.goodsList[index].isPicked;
                  that.goodsList.splice(index, 1, that.goodsList[index]);
    		});
            mui('.pay-detail').on('tap','.pic', function(e){
                let id = this.getAttribute('data-id');
                that.$router.push({name:'ProductDetail',query:{id:id}});
            });
     	},
     	methods:{
     		add(i){
   		         this.goodsList[i].num++;
   		         connect.$emit('addShopCart',1);
   		         localStorageTool.updateProds({
   		             id:this.goodsList[i].id,
   		             num:1, //11
   		         })
   		     },
   		     sub(i){
   		         if(this.goodsList[i].num<= 1) return;
   		         this.goodsList[i].num--;
   		         connect.$emit('addShopCart',-1);//-1
   		         localStorageTool.updateProds({
   		             id:this.goodsList[i].id,
   		             num:-1, //11
   		         })
   		     },
   		     del(i,callback){
   		         //storage,number
   		         let goods = this.goodsList[i];
   		         //storage
   		         localStorageTool.deleteProdById(goods.id)
   		         //number
   		         connect.$emit('addShopCart',-goods.num);
   		         //
   		         this.goodsList.splice(i,1);
   		         callback && callback(true);
   		     },
   		     selectedAll(){
   		     	let checked = true;
                // 
                if(this.isAllSelected){
                    checked = false;
                }
                this.goods = this.goodsList.map(el => {
                    this.$set(el,'isPicked',checked);
                    return el;
                })
   		     },
   		     isPicked(i){
   		     	let goods = this.goodsList[i];
   		     	goods.isPicked = !goods.isPicked;
   		     	var isPickedCount=0;
   		     	this.goodsList.forEach((ele,index)=>{
   		     		if(ele.isPicked){
   		     			isPickedCount++;
   		     		}
   		     	});
   		     	if (isPickedCount == this.goodsList.length){
   		     		this.allSelectedValue = true;
   		     	} else {
   		     		this.allSelectedValue = false;
   		     	}
   		     },
     	},
     	computed:{
        	payment(){
        	    // 1:
        	    // 2:=  * 
        	    // 3:
        	    // 4: +num  
        	    let num = 0;
        	    let sum = 0;
        	    let isEmpty = false;
        	    if (!this.goodsList || this.goodsList.length === 0) {
        	    	isEmpty = true;
        	    } else {
        	    	isEmpty = false;
        	    }
        	    this.goodsList.forEach((ele)=>{
        	        if(ele.isPicked){
        	            //
        	            sum += ele.num * ele.sell_price;//
        	            num += ele.num;//
        	        }
        	    })
        	    return {
        	        num,sum,isEmpty
        	    };
        	},
        	// 
            isAllSelected(){
                return this.goodsList.every((el) => {
                    return el.isPicked;
                })
            },
    	},

    }
</script>
<style scoped>

.W_car_box{
	width: 100%;
    margin: 0 auto;
    text-align: center;
    margin-top: 25%;
    background-color: #fff;
    display: none;
 }

 .W_car_box.show{
 	display: block;
 }
.W_car {
    width: 95%;
}
.W_car img{
	margin: 0 auto;
	display: block;
	width: 80px;
}

.W_car_box p {
    font-size: 16px;
    color: #8a8a8a;
    display: block;
    margin-top: 5%;
}

.W_car_box a {
    font-size: 0.75rem;
    display: block;
    margin-top: 1%;
}

.cart-content{
}
.pay-detail{
	padding-top: 40px;
	padding-bottom: 95px;
}
.cart-product-content{
	width: 100%;
    margin: 0 auto;
    overflow: hidden;
}
.product-selected{
	width: 4%;
    float: left;
    position: absolute;
    top: 50%;
    margin-top: -3%;
}

.mui-table-view:before{
	background-color: unset;
	height: 0px; 
}
.mui-table-view:after{
	background-color: unset;
	height: 0px; 
}
.mui-table-view-cell:after{
	background-color: unset;
	height: 0px; 
}

.cart-item{
	border-bottom: 1px solid #e5e5e5;
}

.product-selected>input{
	visibility:hidden;
}
.product-selected label{
	position: absolute;
	left: 0px;
	top:50%;
	margin-top: -10px;
	width: 20px;
	height: 20px;
	border: 1px solid #e5e5e5;
	border-radius: 50%;
}

/*input,inputlabel*/
.product-selected input:checked+label::before{
	content: "\e86c";
	font-family: icomoon;
	position: absolute;
	font-size: 26px;
	color: #ffc428;
	left: -4px;
	top: -1px;
}

.product-pic{
	float: left;
    width: 33%;
    min-height: 80px;
    margin-left: 12%;
}
.product-pic img{
	width: 100%;
	vertical-align: middle;
}
.product-detail{
	float: right;
    width: 52%;
    height: 100%;
    padding-left: 2%;
    padding-top: 2%;
}
.product-title{
    font-size: 14px;
    color: #282828;
    width: 100%;

    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
}
.product-nums{
	margin-top: 8%;
	width: 100%;
}
.product-nums .product-count{
    font-size: 12px;
    color: #8a8a8a;
}
.product-nums .product-money{
    color: #fd3a9f;
    font-size: 0.65rem;
}

.product-change-count{
	position: absolute;
	right: 0px;
    width: 50%;
    bottom: 0px;
}
.product-change-count span{
	display: inline-block;
    border: 1px solid #e6e6e6;
    border-radius: 3px;
    width: 28.5%;
    height: 40px;
    float: left;
    text-align: center;
    line-height: 40px;
    margin-left: 4%;
}

.product-change-count span:nth-child(1){
	margin-left: 0px;
}

.product-change-count span:nth-child(2) {
    width: 34.5%;
}

.pay-content{
	width: 100%;
    height: 45px;
    background: #fff;
    position: fixed;
    bottom: 50px;
    left: 0;
    margin-top: 10px;
    line-height: 45px;
    border-top: 1px solid #e5e5e5;
    color: #282828;
    font-size: 14px;
}
.selected-all{
	float: left;
    width: 16%;
    overflow: hidden;
    text-align: right;
}

.all{
	position: absolute;
	width: 4%;
    left: 4%;
}


.all>input{
	visibility:hidden;
}
.all label{
	position: absolute;
	left: 0px;
	top:50%;
	margin-top: -10px;
	width: 20px;
	height: 20px;
	border: 1px solid #afafaf;
	border-radius: 50%;
}

/*input,inputlabel*/
.all input:checked+label::before{
	content: "\e86c";
	font-family: icomoon;
	position: absolute;
	font-size: 26px;
	color: #ffc428;
	left: -4px;
	top: -14px;
}

.selected-all{
	margin-left: 5%;
}



.go-pay{
	float: right;
    background: #ffc428;
    font-size:16px;
    color: #fff;
    height: 44px;
    text-align: center;
    line-height: 44px;
    width: 30%;
}

.total-money{
	float: right;
	padding-right: 20px;
}

.total-money span{
	font-size: 14px;
	font-weight: 700;
}

.total-money i{
	color: #ffc428;
}

.W_car_box .goshopping{
    display: inline-block;
    height: 36px;
    font-size: 14px;
}
</style>


